import React, { PureComponent, Suspense } from "react";
import ReactHtmlParser from 'react-html-parser';
import { STRING_TO_URL_CONVERT } from '../../../../utils'
import { isSocialEmbed, isSocialWall, isTagEmbed } from "../../../../actions/api";
import ContentWithOutSlack from './PostContentConvertion'
//const ContentWithOutSlack = React.lazy(() => import('./PostContentConvertion'));
export default class PostContent extends PureComponent {
  constructor(props) {
    super(props);
    this.contentRef = React.createRef();
  }
  componentDidMount() {
    if (isTagEmbed) {
      setTimeout(() => {
        const contentHeight = this.contentRef.current.clientHeight;
        if (this.props.contentRefCallBack) this.props.contentRefCallBack(contentHeight)
      }, 100);
    }
  }


  render() {
    const { contentClass, content, ThemeRule, font, personalization, item, contentTitle, readMore, maxLength, textAlign, maxLines, isReadMore } = this.props;
    //const { expanded } = this.state
    const truncateStyle = {
      WebkitLineClamp: maxLines,
      textAlign: ThemeRule.textAlignment
    };
    let contentData = item.network.id == 20 ? content : (isSocialWall && !isSocialEmbed) ? content : STRING_TO_URL_CONVERT(content);
    if (item.network.id == 12) contentData = ReactHtmlParser(contentData); // for RRS but we have to change from backend.
    const trimClasses = `${ThemeRule.lineTrim ? ` tb_content_line-${ThemeRule.lineTrim}` : ``}`
    const contentClasses = `${contentClass} ${!isReadMore ? trimClasses : ''}`
    return (
      <div className={contentClasses} ref={this.contentRef} style={truncateStyle}>
        {contentTitle ? <div className="tb_bold_txt__">{ReactHtmlParser(contentTitle)}</div> : null}
        <ContentWithOutSlack data={item} content={contentData} Personalization={personalization} ThemeRule={ThemeRule} />
      </div>
    );
  }
}
